<template>
  <div>
    <div class="div_relation">
      <div class="title">分销关系记录:</div>
      <div style="display:flex">
      <div class="div_content">
        <span>关系编号：123</span><br>
        <span>关系类型：123</span><br>
        <span>关系建立时间：123</span><br>
      </div>
      <div class="div_content">
        <span>上级用户ID：082131</span><br>
        <span>下级用户ID：082131</span><br>
        <span>关系状态：082131</span><br>
      </div>
        <div class="div_content">
          <span>上级用户名称：家政</span><br>
          <span>下级用户名称：家政</span><br>
          <span>分销收益总额：100</span><br>
        </div>
        <div class="div_content">
          <span>上级用户昵称：皮卡丘</span><br>
          <span>下级用户昵称：皮卡丘</span><br>
        </div>
        <div class="div_content">
          <span>上级用户类型：家政公司</span><br>
          <span>下级用户类型：家政公司</span><br>
        </div>
      </div>
    </div>
    <div class="div_earnings">
      <div class="title">分销收益记录:</div>
      <div id="table">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="order" label="订单号" align="center"></el-table-column>
        <el-table-column prop="order_date" label="订单时间" align="center"></el-table-column>
        <el-table-column prop="order_type" label="订单类型" align="center"></el-table-column>
        <el-table-column prop="dis_number" label="分销基数" align="center"></el-table-column>
        <el-table-column prop="dis_earnings" label="分销收益占比" align="center"></el-table-column>
        <el-table-column prop="dis_money" label="分销收益金额" align="center"></el-table-column>
      </el-table>
    </div>
    <div id="table_bottom">
        <el-pagination
          @current-change="handleCurrentChange"
          next-text="下一页"
          prev-text="上一页"
          layout="prev, pager, next"
          :total=total>
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['分销管理', '分销列表', '分销详情'])
  },
  data () {
    return {
      page: 1,
      page_size: 10,
      total: 0,
      tableData: []
    }
  },
  methods: {
    // 点击切换页面
    handleCurrentChange (val) {
      this.page = val
      this.getLeaderList()
    }
  }
}
</script>
<style scoped>
.div_relation {
  padding: 30px 10px 30px 30px;
  text-align:left;
  box-sizing: border-box;
}
.div_earnings {
  padding: 10px 10px 30px 30px;
  text-align:left;
  box-sizing: border-box;
}
.title {
  padding-bottom:12px;
  font-size:18px;
  font-weight:600;
  box-sizing: border-box;
}
.div_relation span {
  font-size: 15px;
}
.el-table >>> th {
  background-color: #e3eefa;
  border: rgb(220, 224, 233) solid 1px;
  font-family: "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53";
  color: #000000;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
}
#table_bottom {
  margin-top: 10px;
  text-align: center;
}
.div_content {
  margin-right: 80px;
}
</style>



